import Slider from "react-slick";

const data = [
  {
    id: 0,
    title: "Lorem ipsum dolor sit amet consectetur adipisicing elit.",
    content:
      "Lorem ipsum dolor sit, amet consectetur adipisicing elit. Error ullam esse aliquam officia iste maiores, inventore ut, doloribus provident reprehenderit commodi dolore. Consequuntur, magnam error iste doloribus nam praesentium iusto!",
  },
  {
    id: 1,
    title: "Lorem ipsumur adipisicing elit.",
    content:
      "Loreaa ipsusdmasd doasdaasdesse aliquam officia iste maiores, inventore ut, doloribus provident reprehenderit commodi dolore. Consequuntur, magnam error iste doloribus nam praesentium iusto!",
  },
  {
    id: 2,
    title: "Lamet consectetur adipisicing elit.",
    content:
      "Loreoribus provident reprehenderit commodi dolore. Consequuntur, magnam error iste doloribus nam praesentium iusto!",
  },
];

export default function CarosalOurPrograms() {
  var settings = {
    dots: true,
    infinite: true,
    speed: 1000,
    slidesToShow: 1,
    slidesToScroll: 1,
    appendDots: (dots) => {
      return (
        <ul>
          {dots.map((item) => (
            <li
              key={item.key}
              onClick={item?.props?.children?.props?.onClick}
              className={`transition-all  duration-1000`}
              style={{
                backgroundColor: item?.props?.className ? "#124782" : "white",
                width: item?.props?.className ? "30px" : "14px",
                height: "14px",
                borderRadius: "9999px",
              }}
            />
          ))}
        </ul>
      );
    },
    arrows: false,
  };
  return (
    <div className="text-white p-8 min-w-[300px] max-w-[430px] w-[90vw] text-center md:text-start md:w-[400px]">
      <Slider {...settings} draggable={false} fade={true}>
        {data?.map((item) => (
          <div key={item.id} className="leading-8">
            <div className="font-bold text-xl my-4">{item.title}</div>
            <div className="my-4">
              {item?.content?.slice(0, 170)}
              {item?.content?.length > 170 ? "..." : ""}
            </div>
          </div>
        ))}
      </Slider>
    </div>
  );
}
