import { ReactComponent as Arrow1 } from "../icons/arrow-one.svg";
import { ReactComponent as Arrow2 } from "../icons/arrow-two.svg";
import { ReactComponent as Arrow3 } from "../icons/arrow-three.svg";
import { ReactComponent as Arrow4 } from "../icons/arrow-four.svg";
import { useState } from "react";

const Arrows = () => {
  const [isHovered, setIsHovered] = useState(false);
  return (
    <button
      className="flex items-center group animate-pulse"
      onMouseEnter={() => {
        setIsHovered(true);
      }}
      onMouseLeave={() => {
        setIsHovered(false);
      }}
    >
      <Arrow1
        height={50}
        fillOpacity={isHovered ? 1 : 0.8}
        fill={isHovered ? "#e8a7ff" : "#fff"}
        className="transition-all duration-700 relative left-0 delay-300 group-hover:left-4"
      />
      <Arrow2
        height={50}
        fillOpacity={isHovered ? 0.8 : 0.6}
        fill={isHovered ? "#e8a7ff" : "#fff"}
        className="transition-all duration-700 relative -left-5 delay-200 group-hover:-left-1"
      />
      <Arrow3
        height={50}
        fillOpacity={isHovered ? 0.6 : 0.4}
        fill={isHovered ? "#e8a7ff" : "#fff"}
        className="transition-all duration-700 relative -left-9 delay-100 group-hover:-left-5"
      />
      <Arrow4
        height={50}
        fillOpacity={isHovered ? 0.4 : 0.2}
        fill={isHovered ? "#e8a7ff" : "#fff"}
        className="transition-all duration-700 relative -left-[3.25rem]  group-hover:-left-9"
      />
    </button>
  );
};

export default Arrows;
